.header {
  max-width: 120rem;
  margin: 0 auto;
  padding-top: 3rem;
  text-align: center;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: center;
}
.header ul {
  padding: 1rem 0;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  justify-content: flex-start;
  flex-wrap: wrap;
}
.header ul li {
  margin: 0 1rem;
  text-align: center;
}
.header ul li a {
  padding: 0.5rem 1rem;
  font-size: 2rem;
  display: inline-block;
  color: white;
  text-decoration: none;
  cursor: pointer;
}
