* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}
body {
  font: normal 100% Arial, sans-serif;
  font-size: 14px;
  font-size: 1.4rem;
}
ul,
li {
  list-style: none;
}
.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  justify-content: space-between;
}
.home {
  display: flex;
  display: -webkit-flex;
  /* Safari */
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 40vh;
}
.home .logo {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  overflow: hidden;
}
.home .logo img {
  width: 8rem;
}
footer {
  height: 9.5rem;
}
